  @import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

  *{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
}

html,
body{
  font-size: 16px;
  font-weight: 500;
}

a{
  text-decoration: none;
}

li{
  list-style: none;
}

.d-flex{
  display: flex;
  gap: 10px;
}

button{
  padding: 10px 15px;
  outline: none;
  border: none;
  cursor: pointer;
  font-weight: 700;
  outline: none;
  transition: all 0.2s;
  &:hover{
    opacity: 0.9;
  }
  &:active{
    opacity: 0.5;
  }
}

.t-white{
  color: white;
}


.bg-black{
  background-color: black;
}

nav{
  display: flex;
  justify-content: space-between;
  padding: 15px 25px;
  width: 100%;

  & li{
    cursor: pointer;
  }
}

h1{
  width: 100%;
  font-weight: 700;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
}

main{
  padding: 25px;
  display: flex;
  justify-content: center;
  gap: 10px;
  width: 100%;
}

.main{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
}

.cardSection{
  width: 70%;
  min-width: 150px;
}

.cartSection{
  width: 28%;
  min-width: 150px;
}

.right{
  gap: 20px;
}


/* ----------------------- card ------------------------ */
.card{
  max-width: 250px;
  min-height: 350px;
  border: 1px solid grey;
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  transition: all 0.2s;

  &:hover{
    scale: 1.01;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  & p{
    margin: 5px;
  }

  & img{
    width: 100%;
    margin: 10px;
    aspect-ratio: 1/1;
    margin: auto;

  }

  & .card-btn{
    width: 100%;
    text-align: center;
    padding: 10px;
    position: absolute;
    bottom: 0;
    cursor: pointer;
    transition: all 0.12s ease;

    &:hover{
      opacity: 0.85;
    }

    &:active{
      opacity: 0.7;
    }
  }
  
}

/* ----------------------- cart ------------------------ */

.cart-box{
  padding: 25px;
  max-height: 600px;

  & h3{
    text-align: center;
    font-weight: 500;
    font-size: 1.5rem;
    margin: 1rem;
  }

  
  &>table {
    width: 100%;
    border-collapse: collapse;

    & tbody {
        & tr {

            &:last-child {
                border-top: 1px solid white;
                font-size: 1.2rem;
                padding-top: 5px;
                margin: 8px 0;
            }
        }

        & td {
            padding: 8px;

            &:nth-child(3) {
                text-align: right;
            }
        }
    }
}

  & button{
    width: 100%;
    cursor: pointer;
  }
}


.disabled{
  cursor: not-allowed !important;
}